.wrapper {

  -webkit-transition: background linear .5s;

  transition: background linear .5s;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}
.container {

  -webkit-transition: background linear .5s;

  transition: background linear .5s;
  /* padding: 2rem */
}

.darkwrapper{
  background: rgb(69, 69, 69);
}

.darkcontainer{
  background: rgb(69, 69, 69);
}

#projects {
  margin-top: 10px;
}

#projects li {
  position: relative;
  list-style-type: none;
  width: 27.85714285714286%;
  padding-bottom: 32.16760145166612%;
  float: left;
  overflow: hidden;
  visibility: hidden;
  -webkit-transform: rotate(-60deg) skewY(30deg);
          transform: rotate(-60deg) skewY(30deg); /* skewY   -  skews an element in the vertical direction on the 2D plane. */
}
#projects li:nth-child(3n+2){
  margin:0 1%;
}
#projects li:nth-child(6n+4){
  margin-left:0.5%;
}
#projects li:nth-child(6n + 4),
#projects li:nth-child(6n + 5),
#projects li:nth-child(6n + 6) {
  margin-top: -6.9285714285%;
  margin-bottom: -6.9285714285%;
  -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);
          transform: translateX(50%) rotate(-60deg) skewY(30deg);
}


#projects li:nth-child(6n + 4):last-child,
#projects li:nth-child(6n + 5):last-child,
#projects li:nth-child(6n + 6):last-child {
  margin-bottom:0%;
}

#projects li * {
  position: absolute;
  visibility: visible;
}

#projects li > div {
  width: 100%;
  height: 100%;
  text-align: center;
  color: black;
  overflow: hidden;
  -webkit-transform: skewY(-30deg) rotate(60deg);
          transform: skewY(-30deg) rotate(60deg);
}


#projects li img {
  left: -100%;
  right: -100%;
  width: auto;
  height: 100%;
  margin: 0 auto;
}


/* inverts the colors of the hexagon when applied */
.inverted{
  -webkit-filter: invert(100%);
          filter: invert(100%);
  -webkit-transition: -webkit-filter linear 1s;
  transition: -webkit-filter linear 1s;
  transition: filter linear 1s;
  transition: filter linear 1s, -webkit-filter linear 1s}

#projects div h1, #projects div p{
  width: 100%;
  padding:0 5%;
  background-color:#ffffffd8;
  -webkit-transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
  transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
}

#projects li h1{
  bottom:110%;
  font-size:20px;
  padding-top:100%;
  padding-bottom:100%;
}

#projects li p{
    padding-top:50%;
    top:110%;
    padding-bottom:50%;
}


#projects li div:hover h1 {
  bottom:50%;
  padding-bottom:10%;
}

#projects li div:hover p{
  top:50%;
  padding-top:10%;

}
